import * as React from "react";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Link, navigate } from "gatsby";
import Parse from "../vendor/ParseApp";
import Card from "react-bootstrap/Card";
import { Exception } from "sass";

export default function App() {
  const [message, setMessage] = React.useState("");
  const [model, setModel] = React.useState({});

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
  };

  return (
    <Layout>
      <Container className="">
        <Row>
          <Col xs="0" md="3"></Col>
          <Col xs="12" md="6">
            <Card className="color-brand-1">
              <Card.Header>Login</Card.Header>

              <Card.Body>
                <Form>
                  <Row>
                    <Col xs="12">
                      <Form.Group
                        className="mb-3"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Email Address</Form.Label>
                        <Form.Control
                          type="text"
                          placeholder=""
                          onChange={(v) =>
                            setModelValue("email", v.target.value)
                          }
                        />
                      </Form.Group>
                    </Col>
                    <Col xs="12">
                      <Form.Group
                        className="mb-3"
                        controlId="exampleForm.ControlInput1"
                      >
                        <Form.Label>Password</Form.Label>
                        <Form.Control
                          type="password"
                          placeholder=""
                          onChange={(v) =>
                            setModelValue("password", v.target.value)
                          }
                        />
                      </Form.Group>
                    </Col>
                  </Row>
                  <Button
                    variant="primary"
                    onClick={async () => {
                      try {
                        await Parse.User.logIn(model.email, model.password);
                        const profileItem = await Parse.Cloud.run(
                          "getMyProfile"
                        );
                        console.log(profileItem);
                        if (profileItem) {
                          navigate("/");
                        } else {
                          navigate("/account");
                        }
                      } catch (e: Exception) {
                        setMessage(e?.message);
                      }
                    }}
                  >
                    Login
                  </Button>{" "}
                  <Button
                    variant="primary"
                    onClick={async () => {
                      try {
                        await Parse.User.requestPasswordReset(model.email);
                      } catch (e: Exception) {
                        setMessage(e?.message);
                      }
                    }}
                  >
                    Reset Password
                  </Button>
                  <div>{message}</div>
                </Form>
              </Card.Body>
            </Card>
          </Col>
          <Col xs="0" md="3"></Col>
        </Row>
      </Container>
    </Layout>
  );
}
